@use "@scss/abstracts/variables" as *;
@use "@scss/abstracts/mixins" as *;

%btn {
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding: rem(11px,11px) rem(24px,24px);
	color: $white;
	font-size: $font-size-base;
	font-weight: 700;
	line-height: 1.5;
	text-align: center;
	text-decoration: none;
	border: none;
	border-radius: rem(2px,2px);
	background-color: $primary-100;
	transition: background-color 0.3s ease 0s, color 0.3s ease 0s, border-color 0.3s ease 0s;

	--focuc-outline-space: -1px;
	user-select: none;

	@include respond-below(md) {
		padding: rem(9px,9px) rem(14px,14px);
	}

	&::before {
		content: "";
		position: absolute;
		inset: var(--focuc-outline-space);
		opacity: 0;
		visibility: hidden;
		transition: 0.4s ease;
		border: 1px solid $gray;
		border-radius: rem(5px,5px);
		pointer-events: none;
	}

	&:focus {
		--focuc-outline-space: -4px;
		outline: 0;
		color: $white;

		&::before {
			opacity: 1;
			visibility: visible;
		}
	}

	&:hover {
		color: $primary-100;
		background-color: $secondary-100;
		text-decoration: none;
		cursor: pointer;
	}
}

// Contextual override (must come before :hover)
.bg-dark,
.bg-secondary {
	.btn,
	.btn-link {
		color: $primary-100 !important;
		background-color: $white;

		&:hover {
			background-color: $secondary-100;
		}

		&:focus {
			&::before {
				border-color: $white;
			}
		}
	}
}

.btn {
	@extend %btn;

	&.-dark {
		color: $primary-100 !important;
		background-color: $white;

		&:hover {
			background-color: $secondary-100;
		}

		&:focus {
			&::before {
				border-color: $white;
			}
		}
	}

	&.-sm {
		font-size: rem(12px,12px);
		padding: rem(4px,4px) rem(9px,9px);
	}

	&-link {
		position: relative;
		color: $primary-100;
		font-size: $font-size-base;
		font-weight: 700;
		text-decoration: none;
		border: 0;
		transition: background-color 0.3s ease 0s, color 0.3s ease 0s, border-color 0.3s ease 0s;
		user-select: none;

		&::before {
			content: "";
			position: absolute;
			inset: -4px -8px;
			opacity: 0;
			visibility: hidden;
			transition: 0.4s ease;
			border: 1px solid $gray;
			pointer-events: none;
			border-radius: rem(20px,20px);
		}

		&::after {
			@include icon("c");
			display: inline-flex;
			font-size: em(9px, 16px);
			margin-left: 8px;
			color: $primary-100;
			transition: 0.4s ease;
		}

		&:focus {
			outline: 0;

			&::before {
				opacity: 1;
				visibility: visible;
			}
		}

		&:hover {
			color: $secondary-100;
			text-decoration: none;
			border-color: currentcolor;

			&::after {
				transform: translateX(4px);
				color: $secondary-100;
			}
		}

		a:focus & {
			outline: 0;

			&::before {
				opacity: 1;
				visibility: visible;
			}
		}

		a:hover & {
			color: $secondary-100;

			&::after {
				transform: translateX(4px);
				color: $secondary-100;
			}
		}
	}
}

// Contextual override for link buttons
.bg-dark .btn-link,
.bg-secondary .btn-link,
.btn-link.-dark {
	color: $white;

	&:hover {
		color: $secondary-100;
	}

	&:focus::before {
		border-color: $white;
	}
}

.bg-secondary .btn-link:hover,
.bg-secondary a:hover .btn-link {
	color: $primary-100;

	&::after {
		color: inherit;
	}
}

input[type="button"],
input[type="reset"],
input[type="submit"] {
	@extend %btn;
	appearance: button;
}
